<template>
  <v-container
    fluid
    grid-list-md>
    <v-layout
      row
      wrap>
      <v-flex md12>
        <material-card
          color="primary"
          title="Pesquisar Empresa">
          <v-card class="elevation-0">
            <v-card-text>
              <v-form>
                <v-layout
                  row
                  wrap>
                  <v-flex md3>
                    <v-text-field
                      v-model="filtros.nome"
                      label="Nome Empresa" />
                  </v-flex>
                  <v-flex md3>
                    <v-autocomplete
                      v-model="filtros.tpRepasseId"
                      :items="repasses"
                      label="Tipo de Repasse"
                      item-value="id"
                      item-text="descricao"
                      clearable/>
                  </v-flex>
                  <v-flex md3>
                    <v-autocomplete
                      v-model="filtros.redeId"
                      :items="redes"
                      label="Rede"
                      item-value="id"
                      item-text="descricao"
                      clearable
                    />
                  </v-flex>
                </v-layout>
              </v-form>
            </v-card-text>
            <v-card-actions>
              <v-spacer />
              <v-btn
                :loading="loadingBtn"
                color="primary"
                @click="pesquisar">Pesquisar</v-btn>
            </v-card-actions>
          </v-card>
        </material-card>
      </v-flex>
      <v-flex md12>
        <v-card>
          <v-toolbar
            flat
            color="white">
            <v-toolbar-title>Empresas</v-toolbar-title>
            <v-divider
              class="mx-2"
              inset
              vertical/>
            <v-spacer/>
            <v-btn
              small
              fab
              color="primary">
              <v-icon @click="newItem()">mdi-plus</v-icon>
            </v-btn>
          </v-toolbar>
          <v-progress-linear
            :active="loading"
            color="light-blue"
            indeterminate
            height="3"
          />
          <v-data-table
            :headers="headers"
            :items="items"
            :total-items="totalItems"
            :pagination.sync="pagination"
            class="elevation-1 arrow-pagintion"
          >
            <template
              slot="headerCell"
              slot-scope="{ header }">
              <span
                class="subheading font-weight-light text--darken-3"
                v-text="header.text" />
            </template>
            <template
              slot="items"
              slot-scope="{ item }">
              <td>{{ item.nome }}</td>
              <td>{{ item.tpRepasseDescricao }}</td>
              <td>{{ item.nmRede }}</td>
              <td class="justify-end layout ma-2">
                <v-icon
                  class="mr-2"
                  color="blue"
                  title="Editar dados da empresa"
                  @click="editItem(item)"
                >mdi-pencil</v-icon>
                <v-icon
                  color="red"
                  title="Excluir a empresa"
                  @click="openDialogDelete(item)"
                >mdi-delete</v-icon>
              </td>
            </template>
          </v-data-table>
        </v-card>
      </v-flex>
      <template
        v-if="dialog"
      >
        <v-layout
          row
          justify-center>
          <v-dialog
            v-model="dialog"
            max-width="390">
            <v-card>
              <v-card-title class="headline">Excluir Empresa</v-card-title>

              <v-card-text>Confirma a exclusão da Empresa <span class="title">{{ item.nome }}</span>? Essa operação não poderá ser desfeita.</v-card-text>

              <v-card-actions>
                <v-spacer/>

                <v-btn
                  outline
                  style="color: black !important"
                  @click="closeDialogDelete">Cancelar</v-btn>

                <v-btn
                  color="red"
                  @click="deleteItem">Excluir</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-layout>
      </template>
    </v-layout>
  </v-container>
</template>

<script>
import { EmpresaBusiness, TiposBusiness } from '../../business'

export default {
  metaInfo: {
    titleTemplate: '%s - Empresas'
  },
  data () {
    return {
      dialog: false,
      item: null,
      loading: false,
      totalItems: 0,
      pagination: {
        rowsPerPage: 10,
        sortBy: 'nome'
      },
      filtros: {
        nome: null,
        redeId: null,
        tpRepasseId: null
      },
      redes: [],
      repasses: [],
      search: '',
      headers: [
        {
          text: 'Nome',
          value: 'nome',
          sortable: false
        },
        {
          text: 'Tipo Repasse',
          value: 'tpRepasse',
          sortable: false
        },
        {
          text: 'Rede',
          value: 'nmRede',
          sortable: false
        },
        {
          sortable: false,
          text: ''
        }
      ],
      items: [],
      loadingBtn: false
    }
  },
  watch: {
    pagination: {
      handler () {
        this.paginar()
      }
    }
  },
  methods: {
    reload () {
      if (this.redes.length === 0) {
        TiposBusiness.getAllRedes().then(response => { this.redes = response.data })
      }
      if (this.repasses.length === 0) {
        TiposBusiness.listRepasse().then(response => { this.repasses = response.data })
      }
    },
    newItem () {
      this.$router.push('cad-empresa')
    },
    editItem (item) {
      this.$router.push(`cad-empresa/${item.id}`)
    },
    openDialogDelete (item) {
      this.item = item
      this.dialog = true
    },
    closeDialogDelete () {
      this.item = null
      this.dialog = false
    },
    deleteItem () {
      EmpresaBusiness.delete(this.item.id)
        .then(response => {
          this.$root.showSucesso(response.data)
          this.closeDialogDelete()
          this.paginar()
        })
        .catch(error => this.$root.showErro(error.data))
    },
    pesquisar () {
      this.loadingBtn = true
      this.paginar()
    },
    paginar () {
      this.loading = true
      EmpresaBusiness.pagination(this.pagination.rowsPerPage, this.pagination.page - 1, this.pagination.sortBy || 'nome', this.filtros).then(response => {
        this.items = response.data.content
        this.totalItems = response.data.totalElements
      }).finally(() => {
        this.loadingBtn = false
        this.loading = false
      })

      this.reload()
    }
  }
}
</script>
